import { Link } from "react-router-dom";
import "../css/Navigation.css";


const Navbar = ({children, id}) => {
	
	return (
		<div className="navbar" id={id}>
			{children}
		</div>
	);
}

const Nav = ({children, id, to}) => {
	return (
		<Link className="nav" id={id} to={to}>
			<p> {children} </p>
		</Link>
	);
}

export {Nav, Navbar};