import './css/App.css';

import Frame from './Components/Frame';
import { Navbar, Nav } from './Components/Navigation'
import { useEffect } from 'react';
import { Route, Router, Switch, withRouter } from 'react-router-dom';

import Profile from './Components/Profile';
import CV from './Components/CV';
import { useState } from 'react';

const App = ( { location, history }) => {
	let x, left, down = false;
	const scroll_snap_screen = "screen-horizon";
	const menu = ["/", "/cv"];
	let pos = 0;
	
	
	const listenerMousedown = e => {
		e.preventDefault();
		down = true;
		x = e.pageX;
		left = document.getElementById(scroll_snap_screen).scrollLeft;
	}
	
	const listenerMouseup = e => {
		down = false;
		let newX = e.pageX;
		const screen = document.getElementById(scroll_snap_screen);
		
		// 일정 범위를 스크롤해야 반응함.
		if (Math.abs(newX - x) > window.innerWidth / 4){
			// 왼쪽으로 이동
			if (newX - x > 0){
				if (pos > 0)
					pos--;
			}
			// 오른쪽으로 이동
			else{
				if (pos < menu.length - 1)
					pos++;
			}
			
			const curPath = menu.find((e, index, arr) => index == pos)
			history.push(curPath)
		}
	}
	
	useEffect(() => {
		window.addEventListener("mousedown", listenerMousedown);
		window.addEventListener("mouseup", listenerMouseup);
		
		
		return () => {
			window.removeEventListener("mousedown", listenerMousedown);
			window.removeEventListener("mouseup", listenerMouseup);
		}
	}, [])
	
	useEffect(() => {
		const screen = document.getElementById(scroll_snap_screen);
		
		let pathPos = menu.findIndex(e => e == location.pathname)
		if (pathPos == -1){
			history.push("/")
		}
		
		screen.scrollTo({
				left: window.innerWidth * pathPos,
				behavior: 'smooth'
			});
	}, [location.pathname])
	
	return (
		<div className="App">
			<Navbar>
				<Nav to="/"> 프로필 </Nav>
				<Nav to="/cv"> CV </Nav>
			</Navbar>
			<div className="guard"></div>
			<Switch>
				<div id={scroll_snap_screen}>
					<Frame>
						<Route exact={true} path="/" component={Profile}></Route>
					</Frame>
					<Frame >
						<Route exact={true} path="/cv" component={CV}></Route>
					</Frame>
				</div>
			</Switch>
		</div>
	);
}

export default withRouter(App);
